import { IValidationResult } from "./../../models/models";

export const nameValidation = (newValue: string): IValidationResult => {
  const res: IValidationResult = {
    result: false,
    errorMsg: "",
  };

  if (newValue.trim().length === 0) {
    res.result = true;
    res.errorMsg = "   ";
    return res;
  }

  if (/[^a-zA-Za-A-]/.test(newValue)) {
    res.result = true;
    res.errorMsg = "      ";
    return res;
  }

  return res;
};

