export const smallAboutMeValidation = (newValue: string): IValidationResult => {
  const res: IValidationResult = {
    result: false,
    errorMsg: "",
  };

  if (newValue.trim().length <= 0) {
    res.result = true;
    res.errorMsg = "    ";
    return res;
  } 
  if (newValue.length >= 100) {
    res.result = true;
    res.errorMsg = "    (100)";
    return res;
  }
  return res;
};

export const bigAboutMeValidation = (newValue: string): IValidationResult => {
  const res: IValidationResult = {
    result: false,
    errorMsg: "",
  };

  if (newValue.trim().length <= 0) {
    res.result = true;
    res.errorMsg = "    ";
    return res;
  }

  if (newValue.length >= 599) { 
    res.result = true;
    res.errorMsg = "    (300)";
  }

  return res;
};
